#Projects-container{
    width: 100%;
    height: 1500px;
    -ms-flex-align: center;
        align-items: center;
}

#Projects-title{
    background-color: transparent;
    text-align: center;
    margin-top: 70px;
    font-size: 3.4vw;
    color: white;
}
#Projects-background{
    margin: 0;
    position: absolute;
    width: 100%;
    height: 1500px;
    background-image: url('./assets/projects-bg.jpg');
    background-size: cover;;
    -webkit-filter: blur(3px);
    filter: blur(3px);
}

#Projects-overlay{
    margin: 0;
    margin-top: -18px;
    position: absolute;
    width: 100%;
    height: 1500px;
    background-color: rgba(0, 0, 0, 0.3);
}
#Projects-elements::-webkit-scrollbar {
    display: none;
}

.Project-Photo{
    width: 95%;
}

#Projects-elements{
    width: 100%;
    margin: auto;
    margin-top: 50px;
    height: 90%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    overflow: scroll;
}
@media (max-width: 900px){
    .Project-Photo-Container{
        width: 90% !important;
        height: 50vw !important;
        margin-top: 30px;
        margin-bottom: 30px;
    }

}
.Project-Photo-Container{
    width: 45%;
    height: 25vw;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    border-style: solid;
    border-color: white;
    /* background-color: rgba(30, 131, 108, 0.6); */
    background-color: rgba(246, 246, 246, 0.5);
    -webkit-transition: background-color 300ms, border-color 300ms; /* Safari */
    -o-transition: background-color 300ms, border-color 300ms;
    transition: background-color 300ms, border-color 300ms;
    cursor: pointer;
}

.Project-Photo-Container:hover{
    /* background-color: rgba(30, 131, 108, 0.9);
    border-color: rgba(30, 131, 108, 0.9); */
    background-color: rgba(103, 152, 149, 0.9);
    border-color: rgba(103, 152, 149, 0.9);
}


